            Quinta à Terça Feira 18:00 às 00:30 Contato: (038) 3216-2313
          </div>
        </grid-vue>

        <grid-vue :size="{col:[3,0,0],offset:[0,0,0], opts:''}">
          <button class="heartButton"><i class="far fa-heart"></i></button>
          <!-- <button class="heartButton"><i class="fas fa-heart"></i></button> -->
          <img  id="cardImgRight" src="https://www.socialplace.com.br/images/logo.png" >
        </grid-vue>
        <input-search id="searchSticky"/>

      </div> 
    </div> 

  </div>
  
</template>

<script>
// @ is an alias to /src
export default {
  name: 'navbarPhone',  
  props:[],
  data () {
    return {
       cardImg : null,
       cardImgRight : null,
       searchSticky : null,
    }
  },
  methods: {

    switchImg (cardImgOpacity, cardImgRightOpacity) {

      this.cardImg.style.opacity = `${cardImgOpacity}`;
      this.cardImgRight.style.opacity = `${cardImgRightOpacity}`; 
      
    },

    changeImgCard (scrollPosition) {
              
      (scrollPosition > this.cardImg.offsetTop) ? this.switchImg(0,1) : this.switchImg(1,0);
    },

    stickyInputSearch (scrollPosition) {

      //const topPosition = this.searchSticky.getBoundingClientRect().y +100;  //Need to be improve this function
      if (scrollPosition > 260) {
          this.searchSticky.style.position = "fixed";
          
      }else {
        this.searchSticky.style.position = "static";
      } 
    }
   
  },

  mounted() { 

    this.cardImg = document.getElementById("cardImg");
    this.cardImgRight = document.getElementById("cardImgRight");
    this.searchSticky = document.getElementById("searchSticky");

    this.$store.subscribe((mutation,state) =>{

      this.changeImgCard (state.scrollPosition);
      this.stickyInputSearch (state.scrollPosition);
    
    });





  },
}
</script>

